var _ = require('./util');
var extend = _.extend;

/**
 * The exposed Vue constructor.
 *
 * API conventions:
 * - public API methods/properties are prefiexed with `$`
 * - internal methods/properties are prefixed with `_`
 * - non-prefixed properties are assumed to be proxied user
 *   data.
 *
 * @constructor
 * @param {Object} [options]
 * @public
 */

function Vue(options) {
    this._init(options); //* ./instance/init
}

/**
 * Mixin global API
 */

extend(Vue, require('./api/global'));

/**
 * Vue and every constructor that extends Vue has an
 * associated options object, which can be accessed during
 * compilation steps as `this.constructor.options`.
 *
 * These can be seen as the default options of every
 * Vue instance.
 */

Vue.options = {
    directives: require('./directives'),
    filters: require('./filters'),
    partials: {},
    transitions: {},
    components: {},
};

/**
 * Build up the prototype
 */

var p = Vue.prototype;

/**
 * $data has a setter which does a bunch of
 * teardown/setup work
 */

Object.defineProperty(p, '$data', {
    get: function () {
        return this._data;
    },
    set: function (newData) {
        this._setData(newData);
    },
});

/**
 * Mixin internal instance methods
 */

//* copy things to prototype of Vue
extend(p, require('./instance/init'));
extend(p, require('./instance/events'));
extend(p, require('./instance/scope')); //* this._initScope();  //都绑到vue上，但是实在_init里call
extend(p, require('./instance/compile')); //* _bindDir应该就是在这里添加上的

/**
 * Mixin public API methods
 */

extend(p, require('./api/data'));
extend(p, require('./api/dom'));
extend(p, require('./api/events'));
extend(p, require('./api/child'));
extend(p, require('./api/lifecycle')); //* $mount is added here

module.exports = _.Vue = Vue;
